<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/floorMange/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>设置首页楼层展示数据</li>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" class="error__inline">
        <el-form-item label="楼层标题" prop="floorName" :error='errorMessage["floorName"]'>
          <el-input v-model="formData.floorName" maxlength="50"></el-input>
          <el-color-picker v-model="formData.themeColor" color-format="rgb" style="vertical-align: middle;position: relative; top: -1px;"></el-color-picker>
        </el-form-item>
        <el-form-item label="副标题" prop="positionCode" :error='errorMessage["positionCode"]'>
          <el-input v-model="formData.subtitle" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="布局模式" prop="isEnable">
          <el-radio-group v-model="formData.layoutType">
            <el-radio :label="1">宽体布局</el-radio>
            <el-radio :label="2">窄体布局</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error='errorMessage["sortNum"]'>
          <el-input v-model="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="是否显示" prop="isShow" :error='errorMessage["isShow"]'>
          <cms-switch v-model="formData.isShow"></cms-switch>
        </el-form-item>
        <el-form-item label="广告图" prop="adId">
          <el-select v-model="formData.adId" size="mini">
            <el-option-group v-for="(group,index) in advertList" :key="index" :label="group.positionAttrName">
              <el-option v-for="item in group.advertisementsAttr" 
               :key="item.id" 
               :label="item.title" 
               :value="item.id">
              </el-option>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item label="推荐分类">
          <category-tree v-model="formData.categoryData" :selectData="categoryData"></category-tree>
        </el-form-item>
        <el-form-item label="说明" prop="remark" :error='errorMessage["remark"]'>
          <el-input v-model="formData.remark" type="textarea" :rows="4" maxlength="200"></el-input>
        </el-form-item>
        <!-- 测试tabs -->
        <floor-tabs @change="getTabsData" :selectData="selectPanelLayOuts"></floor-tabs>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update" v-color >保存</cms-button>
        <cms-button type='rest' @click="rest" v-if="type=='save'">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import {
  indexlayoutUpdate,
  indexlayoutGet,
  advertByGroupList
} from '@/api/marketing/index.js'
import advertSelect from './components/advertSelect'
import floorTabs from './components/floorTabs'
import categoryTree from './components/categoryTree'
var defaultData = {
  floorName: '',
  subtitle: '',
  themeColor: '',
  layoutType: 1,
  isShow: true,
  adId: '',
  categoryData: [],
  remark: '',
  sortNum: 10
}
export default {
  mixins: [form],
  components: {
    floorTabs,
    categoryTree,
    advertSelect
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        sortNum: [rules.required(), rules.number()],
        isShow: [rules.required()],
        floorName: [rules.required('请填写楼层标题')],
        adId: [{ required: true, message: '请选择一个广告', trigger: 'change' }]
      },
      categoryData: [], // 分类数据
      advertList: [], // 广告列表
      panelLayouts: [], // 楼层信息
      selectPanelLayOuts: [],
      adPostitionId: ''// 广告版位的id
    }
  },
  methods: {
    getFloorInfo(id) { // 获取首页楼层设置的详情
      indexlayoutGet({ id: id }).then(res => {
        if (res.code === 200) {
          // 数据结构不同赋值
          const data = res.data.indexLayout
          const formData = {
            id: data.id,
            floorName: data.floorName,
            subtitle: data.subtitle,
            themeColor: data.themeColor,
            layoutType: data.layoutType,
            isShow: data.isShow,
            adId: data.adId,
            categoryData: JSON.parse(data.categoryData),
            remark: data.remark,
            sortNum: data.sortNum || 10
          }
          this.categoryData = JSON.parse(data.categoryData)
          this.selectPanelLayOuts = res.data.penelLayoutVOs
          this.formData = Object.assign({}, formData)
          this.formatPanelLayouts(res.data.penelLayoutVOs)
        }
      })
    },
    getAdvertsList() {
      advertByGroupList().then(res => {
        if (res.code === 200) {
          this.advertList = res.data
        }
      })
    },
    getTabsData(data) {
      // 楼层分类信息
      this.panelLayouts = Object.assign([], data)
    },
    formatPanelLayouts(data) { // 绑定关联楼层版面(获取和修改的时候调用)
      const arr = []
      data.forEach(v => {
        const obj = {
          id: v.id || '',
          title: v.title,
          layoutType: v.layoutType,
          adPostitionId: v.adPostitionId || null,
          brandIds: v.brandIds.toString(),
          productData: v.productData
        }
        this.adPostitionId = v.id
        arr.push(obj)
      })
      return arr
    },
    update() {
      const params = {
        indexLayout: Object.assign({}, this.formData),
        penelLayouts: this.formatPanelLayouts(this.panelLayouts)
      }
      this.updateForm(indexlayoutUpdate, params, '/floorMange/list', 'list')
    }
  },
  created() {
    this.getFloorInfo(this.id)
    this.getAdvertsList()
  }
}
</script>
 <style lang="scss" scoped>
</style>




